<template>
  <div id="detail">
    <div id="header">
        <span @click="back()"><i class="el-icon-arrow-left"></i></span>
        <span></span>
    </div>
    <ul id="cont">
      <li class="answer" v-for="(value, index) in question" :key="index">
        <h4>{{value.que}}</h4>
        <p>{{value.ans}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'detail',
  data() {
      return {
          question:[
            {
              que:"Q1: ?",
              ans:",,"
            },
            {
              que:'Q2: ?',
              ans:"20"
            },
            {
              que:'Q3: ',
              ans:"1,312,3130"
            },
            {
              que:'Q4: ?',
              ans:"App&gt;&gt;"
            },
            {
              que:'Q5: ?',
              ans:""
            },
          ]
      }
  },
  methods: {
    back () {
      this.$router.go(-1);
    }
  },
}
</script>
<style scoped>
.answer>p{
  color: #666;
  font-size: 0.16rem;
  margin-top: 0.2rem;
  font-family: Helvetica Neue,Tahoma,Arial;
}
#cont {
  margin-top: 0.7rem;
}
#cont>li {
    width: 90vw;
    margin-left: 3vw;
    margin-top: 0.3rem;
}
#header {
    width: 100%;
    height: 0.5rem;
    background: #3190e8;
    color: white;
    position: fixed;
    top: 0;
}
#header>span:nth-child(1) {
    line-height: 0.5rem;
    margin-left: 5vw;
    font-size: 0.2rem
}
#header>span:nth-child(2) {
  line-height: 0.5rem;
  font-weight: 800;
  margin-left: 30vw;
  font-size: 0.2rem;
}
</style>



